import React from 'react';
import noImage from '../img/no-image.jpg';


class ElementList extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            hover: true,
            imgClick: false,
        }
    }


    toggleHover() {
        this.setState((prevstate) => ({ ...prevstate, hover: true }));
    }

    toggle() {
        this.setState((prevstate) => ({ ...prevstate, hover: false }));
    }

    imgMediumOrOriginal() {
        this.setState((prevstate) => ({ ...prevstate, imgClick: !this.state.imgClick }));
    }

    imageMuvie() {
        if (this.props.show.image === null) {
            return noImage;
        } else {
            return this.props.img['medium'];
        }
    }

    imageMuvieOriginal() {
        if (this.props.show.image === null) {
            return noImage;
        } else {
            return this.props.img['original'];
        }
    }

    genresMuvie() {
        if (this.props.show.genres.length > 0) {
            return this.props.show.genres.join(', ');
        }
        else {
            return "No-ifo";
        }
    }


    render() {

        var linkStyle;
        if (this.state.hover) {
            linkStyle = { opacity: '0' }
        } else {
            linkStyle = { opacity: '1' }
        }

        var srcImg;
        var styleImg;
        var styleDiv;
        if (!this.state.imgClick) {
            srcImg = this.imageMuvie();
            styleImg = 'movie-img';
            styleDiv = '';
        } else {
            srcImg = this.imageMuvieOriginal();
            styleImg = 'movie-img-oroginal';
            styleDiv = 'div-img';
        }


        return (
            <div className="movie"  onMouseOver={this.toggle.bind(this)} onMouseLeave={this.toggleHover.bind(this)} >
                <div className={styleDiv}><img className={styleImg} onClick={this.imgMediumOrOriginal.bind(this)} src={srcImg} alt="movie" /></div>
                <h2 className="movie-name">{this.props.show.name}</h2>
                <p className="movie-year">{this.props.year}</p>
                <p className="movie-genres">{this.genresMuvie()}</p>
                <div style={linkStyle} className="bluebell"></div>
            </div>
        )
    }
}

export default ElementList;